.sidebar{
    flex: 0.2;
    display: flex;
    flex-direction: column;
    height: 100vh;
    overflow-y: auto;
}
.Post{
    color: white;
    background-color: #00b7ff;
    font-size: 1.5em;
    border: none;
    padding: 0.5em 3.2em;
    border-radius: 9999px;
    cursor: pointer;
    font-weight: 600;
    margin-top: .5em;
    margin-left: 0.5em;
    width: 80%;
    max-width: 6rem;
    display: flex;
    align-items: center;
    justify-content: center;
}