.SidebarOption{
    margin-left: 0.5em;
    color: white;
    display: flex;
    width: fit-content;
    padding: 10px;
    border-radius: 9999px;
    cursor: pointer;
    user-select: none;
    margin-bottom: 5px;
    font-family: Arial, Helvetica, sans-serif;
    justify-content: center;
}
.SidebarOption:hover{
    background-color: #3333336e;
}
.SidebarOption:focus{
    background-color: #3333336e;
}
h1{
    font-size: 1.5em;
    margin: 0;
    margin-left: 5px;
}

i{
    font-size: 1.5em;
    margin-right: 0.4em;
}

@media screen and (max-width: 640px) {
    .SidebarOption h1{
        display: none;
    }
    .Post{
        padding: 0.8em;
        font-size: 1.2em;
    }
}