.post{
    border-bottom: solid 1px #4d4d4d;
    padding: 0;
    display: flex;
}
.post .avatar{
    width: 3.3em;
}
.post .post__info{
    flex: 1;
    display: flex;
    flex-direction: column;
}
.post .post__info .user__info{
    display: flex;
    margin-top: 0.2em;
}
.post .post__info .user__info h1{
    font-size: 1.1em;
    font-weight: 300;
    margin: .1em;
}
.post .post__info .user__info h2{
    font-size: 1.1em;
    font-weight: 200;
    margin: .1em;
    color: #494949;
}
.post .post__info .description{
    margin: 0.4em 0.1em;
    font-weight: 200;
}
.post .post__info .post__pic{
    width: max-content;
    max-width: 95%;
    max-height: 30em;
    object-fit: cover;
    border-radius: 0.5em;
}


.post .avatar img{
    width: 75%;
    aspect-ratio: 1/1;
    object-fit: cover;
    border-radius: 9999px;
    margin: 5px ;
}
.post__options{
    width: 40%;
    display: flex;
    padding: 5px 0;
}
.post__options i{
    cursor: pointer;
    font-size: 1em;
    margin-right: 0.33em;
    width: 1.8em;
    height: 1.8em;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 9999px;
}
.post__options .comment:hover{
    color: #2773ff;
    background-color: #2773ff1f;
}
.post__options .like:hover{
    color: #ff1818;
    background-color: #f500001f;
}
.post__options .retweet:hover{
    color: #09ff00;
    background-color: #16bb001f;
}